  padding: 15px;
  background-color: #33333333;
  border: solid 1px #313233;
  -webkit-transition: 256ms;
  transition: 256ms; }
  input.button {
    min-width: 10px; }
    input.button.red {
      -webkit-filter: drop-shadow(0 0 red);
              filter: drop-shadow(0 0 red); }
    input.button.green {
      -webkit-filter: drop-shadow(0 0 green);
              filter: drop-shadow(0 0 green); }
    input.button.blue {
      -webkit-filter: drop-shadow(0 0 blue);
              filter: drop-shadow(0 0 blue); }
  input:focus {
    -webkit-filter: brightness(130%);
            filter: brightness(130%); }
  input[type="submit"] {
